@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,500;0,700;1,300&display=swap);
.coin__info {
  display: flex;
  flex-direction: row;
  border-radius: 4px;
  box-shadow: 0px 6px 11px 0px rgba(63, 81, 181, 0.1);
}

.coin-info__list {
  background: #ffffff;
  padding: 0.7rem;
  flex: 1 1;
}

.coin-info__list {
  font-size: 1.5rem;
  font-weight: 600;
  letter-spacing: 3px;
  color: gray;
}

.coin-info__list .coin-info-title {
  font-size: 1rem;
  font-weight: 500;
  color: black;
  padding-bottom: 10px;
  text-transform: uppercase;
}

.coin-dropdown {
  width: auto;
  flex: 1 1;
  background: lavender;
  text-align: center;
}

.coin-dropdown .coin-select {
  width: 100%;
  height: 100%;
  outline: 0px;
  font-size: 2rem;
  color: #3f51b5;
  background-color: lavender;
}

.coin-chart {
  background: #fff;
  margin-top: 1rem;
  border-radius: 4px;
  padding: 1.5rem;
  box-shadow: 0px 6px 11px 0px rgba(63, 81, 181, 0.1);
  display: flex;
  flex-direction: column;
}

.coin-chart > div {
  min-width: 90%;
  width: 90%;
  margin: 0 auto;
  padding: 1rem;
}

.coin-chart .coin-filter {
  display: flex;
  align-items: center;
}

.coin-name {
  font-size: 1.5rem;
  font-weight: 600;
  letter-spacing: 2px;
  margin: 1rem 0rem;
  color: #3f51b5;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.coin-name .last-update-info .last-updated {
  background: green;
  padding: 2px;
  border: 2px solid green;
  color: white;
  margin-left: 10px;
  border-radius: 4px;
}
.coin-iso,
.last-udpate-title {
  opacity: 0.5;
  padding-left: 4px;
  font-size: 1rem;
  color: gray;
}

.color-primary {
  color: #3f51b5;
}

.chart-filters {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: calc(100% - 10%);
  margin: 0 auto;
}

.chart-filters .chart-filters-heading {
  font-size: 1.2rem;
  color: #3f51b5;
  font-weight: 500;
}

.footer-container {
  background: #ffffff;
  margin: 1rem 0rem 0.5rem 0rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem;
}
.footer-profile {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.copy-right-content {
  display: flex;
  align-items: center;
}

ul li {
  list-style: none;
  line-height: 1;
}

.social-list {
  display: flex;
  flex-direction: row;
  align-items: center;
}

a {
  text-decoration: none;
  display: block;
}
.social-link {
  margin-right: 10px;
  cursor: pointer;
}

.social-link .social-icon {
  height: 20px;
  width: 20px;
  object-fit: cover;
}

.social-link:hover {
  transform: scale(1.1);
  transition: 600ms all;
}

@media only screen and (max-width: 736px) {
  .container {
    width: 90%;
  }
}

@media only screen and (max-width: 562px) {
  .container {
    width: 90% !important;
  }
  .coin__info {
    flex-direction: column;
  }
  .coin-name {
    flex-direction: column;
  }
  .last-update-info {
    display: flex;
    flex-direction: column;
    width: 100%;
    text-align: center;
  }
  .last-udpate-title {
    margin: 1rem 0rem;
  }
}

* {
  margin: 0;
  padding: 0;
}

body {
  background: #f2f5fa;
  font-family: "Roboto", sans-serif;
}

.container {
  width: calc(100% - 200px);
  margin-left: auto;
  margin-right: auto;
}
.header-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

